// Normally mainpage styles

+font(ubuntuB, ubuntu-b-webfont)
=ubuntuB
	font-family: 'ubuntuB', sans-serif

+font(ubuntuR, ubuntu-r-webfont)
=ubuntuR
	font-family: 'ubuntuR', sans-serif

*	
	+ubuntuR
	text-align: center
	
.wrapper
	max-width: 1052px
	margin: 0px auto
	+r(640)
		max-width: 560px
	+r(320)
		max-width: 280px
.anchor
	display: block
	width: 33px 
	margin: 0 auto 10px
	+s(anchor-down)
	&:hover
		+s(anchor-down-hov)

h1, h2
	+ubuntuB
	line-height: 1

[class$='wrap']
	+clr

.header
	+clr
	position: relative
	height: 100%
	padding: 15px 60px 0px
	background: url(../img/header_bg.jpg) no-repeat 0px 0px
	background-size: cover
	&.is-notfull
		height: auto
		background: #205672
		.header__head
			background: #205672
	&__head
		+clr
		max-width: 100%
		margin: 0 auto
		position: fixed
		top: 0px
		left: 0px 
		right: 0px
		padding: 15px 60px 0px
					
		.header__social
			display: block
			float: right
			margin-left: 15px
			margin-bottom: 0px
			&.is-visible
				display: block
		&.is-fixed
			background-color: #205672
			z-index: 100
	
	&__inner
		margin-top: 150px
		height: auto
	&__logo
		float: left
		text-decoration: none
		&:after
			content: ''
			display: block
			+s(logo)
	
	nav
		width: 1052px
		margin: 0 auto

	&__menu
		float: left
		margin-top: 10px
		white-space: nowrap
	&__menu-inner
		display: inline-block
		width: 100%
	&__link
		float: left
		margin-right: 90px
		&:first-child
			margin-right: 100px
		&:last-child
			margin-right: 0px
		a
			+ubuntuR
			line-height: 1
			font-size: 15px
			color: #9fc3da
			text-decoration: none
		&:hover a, a.is-active
			color: #fefefe
		&.ic-arrow
			height: 30px
			position: relative
		&.ic-arrow.is-active .header__submenu
			display: block
		&.ic-arrow>a:after
			content: ''
			display: inline-block
			+s(arrow-down)
			margin-left: 5px
			vertical-align: middle
		&.is-active.ic-arrow>a:after
			transform: scale(-1)
				
	&__submenu
		display: none
		position: absolute
		margin-top: 25px
		top: 0px
		left: 0px
		width: 306px
		table-layout: fixed
		border-collapse: collapse
		.header__course-date
			display: none
		&__link
			width: 101px
			border: 2px solid transparent
			a
				display: inline-block
				width: 100%
				height: 24px
				padding: 6px 0
				+ubuntuB
				font-size: 12px
				line-height: 15px
				color: #fff
				text-transform: uppercase
				span
					text-transform: capitalize
			$color-map: (green: #15b55a, yellow: #f1c40f, violet: #9525a6, purple: #5e1f9f, darkblue: #3e62aa, skyblue: #599bee)
			@each $color, $value in $color-map
				&.is-bg-#{$color} a
					background: $value
				&.is-bg-#{$color} a:hover, &.is-bg-#{$color} a:hover span
					background: #fff
					color: $value
					transition: all .2s

	&__language
		float: right
		margin-top: 10px
		a
			+ubuntuR
			line-height: 1
			font-size: 14px
			color: #9fc3da
			text-decoration: none
			&.is-active, &:hover
				color: #fefefe
			&:first-child
				&:after
					content: '|'
					margin-left: 3px
					cursor: default
					color: #9fc3da

	&__title
		font-size: 44px
		margin-bottom: 5px
		color: #fff
	
	&__description
		font-size: 26px
		margin-bottom: 20px
		color: #fff
	
	&__date
		+ubuntuB
		font-size: 18px
		font-weight: bold
		line-height: 1
		margin-bottom: 25px
		color: #fff
	
	&__button
		display: inline-block
		width: 230px
		padding: 15px 0px
		margin-bottom: 15px
		background: #be3542
		+ubuntuB
		font-size: 16px
		text-transform: uppercase
		text-decoration: none
		font-weight: bold
		color: #fff
		&:hover
			border: 1px solid #be3542
			border-bottom: 5px solid #be3542
			padding-top: 14px
			padding-bottom: 10px
			background: #fff
			color: #be3542
			transition: color .2s, background .2s
	
	&__social
		font-size: 0px
		margin-bottom: 40px	
	
	&__ic
		display: inline-block
		margin-right: 10px
		&:last-child
			margin-right: 0px
		+s(head-fb)
		&:hover
			+s(head-fb-hov)
		&.ic-tw
			+s(head-tw)
			&:hover
				+s(head-tw-hov)
	
	&__boxes-wrap
		+clr
		display: inline-block
		width: 695px
		margin-bottom: 35px
	
	&__box
		float: left
		margin-right: 4px
		position: relative
		height: 64.5px
		background: transparent
		padding-top: 17px
		color: #fff
		border-right: 1px solid #869ca6
		border-left: 1px solid #869ca6
		&:last-child
			margin-right: 0px
		&:before, &:after
			content: ''
			position: absolute
			display: block
			width: (227/2-90/2)+px
			border-bottom: 1px solid #869ca6 
			top: 0
			left: 0
		&:after
			width: (227/2-90/2)+px
			left: (227/2-90/2+90)+px
		.header__item
			float: left
			position: relative
			width: 112px
			padding: 24px 0px 10px
			border-bottom: 1px solid #869ca6
			border-right: 1px solid #869ca6
			border-left: 1px solid #869ca6
			font-size: 13px
			text-transform: uppercase
			text-decoration: none
			line-height: 1
			color: #fff
			&:first-child
				margin-right: 3px
				border-left: none
			&:nth-child(2n)
				border-right: none
			span
				text-transform: none
			&:before
				content: ''
				display: block
				position: absolute
				top: 0
				width: 100%
				height: 13px
			$color-map: (green: #15b55a, yellow: #f1c40f, violet: #9525a6, purple: #5e1f9f, darkblue: #3e62aa, skyblue: #599bee)
			@each $color, $value in $color-map
				&.is-bg-#{$color}:before
					background: $value
				&.is-bg-#{$color}:hover, &.is-bg-#{$color}.is-active
					background: $value
					transition: background .2s
				&.is-bg-#{$color}.is-active:after
					content: ''
					display: block
					position: absolute
					bottom: -17px
					width: 100%
					border-top: 17px solid $value
					border-left: 54px solid transparent
					border-right: 54px solid transparent
		.header__course-date
			position: absolute
			top: -10px
			left: (227/2-90/2)+px
			width: 90px
			height: 20px
			line-height: 18px
			vertical-align: middle
			background: transparent
			border: 1px solid #869ca6

	+r(1720)
		nav
			width: 690px
		&__link
			margin-right: 30px
			&:first-child
				margin-right: 25px

	+r(1180)
		padding: 15px 0px 0px
		background: url(../img/header_bg.jpg) no-repeat -390px 0px
		background-size: cover
		&.is-notfull
			background-size: auto
		&__head
			max-width: 100%
			padding: 15px 0px 0px
			.header__social
				display: none
		&__head-inner
			max-width: 1052px
			margin: 0 auto
		nav
			width: auto
		&__menu
			margin-left: 145px
			&.is-mini, &.is-mini-static
				margin-left: 85px	

	+r(1060)
		&__head
			min-width: 940px
			.header__social.is-visible
				display: none
		&__menu
			margin-left: 50px
			&.is-mini, &.is-mini-static
				margin-left: 50px
	
	+r(940)
		padding: 8px 40px
		background: url(../img/header_bg.jpg) no-repeat -650px 0px
		background-size: cover
		&__head
			position: static
			max-width: 100%
			min-width: auto
			margin: 0 auto
			.header__social.is-visible
				display: none
			.header__menu
				float: right
				width: 56px
				height: 41px
				margin: 0px
				overflow: hidden
				+s(menu)
				cursor: pointer
				.header__link
					display: none
			.header__language
				display: none
			&.is-open
				.header__logo
					display: none
				.header__menu
					position: absolute
					top: 0px
					right: 0px
					left: 0px
					width: auto
					height: auto
					padding-top: 105px
					padding-bottom: 145px
					background: #145b97
					z-index: 100
					cursor: default
					.header__link
						float: none
						display: block
						width: 100%
						border-top: 1px solid #1d6aaa
						&:first-child
							margin-right: 0px
						&:last-child
							border-bottom: 1px solid #1d6aaa
						a
							display: block
							width: 100%	
							padding: 23px 0px
							font-size: 24px
							text-transform: uppercase
						&.ic-arrow
							height: auto
							padding: 0px 0px
						&.ic-arrow>a:after
							margin-left: 30px
							transform: scale(2)
						&.ic-arrow:hover>a:after
							transform: scale(2)
						&.ic-arrow.is-active .header__submenu
							display: block
					.header__submenu
						display: none
						position: static
						width: 542px
						margin: 2px auto 25px
						table-layout: fixed
						border-collapse: separate
						border-spacing: 8px 3px	
						.header__course-date
							display: table-cell
							line-height: 1
							height: 24px
							vertical-align: middle
							background: transparent
							border: 1px solid #869ca6
							font-size: 18px
							color: #e6e6e6
						&__link
							width: 170px
							border: none
							a
								height: 48px
								padding: 18px 0px 0px
								font-size: 16px
								line-height: 1
				.header__language
					display: block
					position: absolute
					top: 648px
					left: 50%
					transform: translate(-50%, 0)
					margin-top: 0px
					z-index: 100
					font-size: 36px
					&.is-open
						top: 790px
					a
						font-size: 36px
						&:first-child:after
							content: ''
							margin-left: 0px
						&:last-child
							margin-left: 55px
				.header__social
					display: block
					position: absolute
					top: 38px
					left: 50%
					transform: translate(-50%, 0)
					margin-left: 0px
					z-index: 100
					.header__ic
						display: inline-block
						margin-right: 45px
						&:last-child
							margin-right: 0px
						&.ic-fb
							+s(soc-fb-640)
						&.ic-tw
							+s(soc-tw-640)
						border: 1px solid #fff
				.header__menu-close
					position: absolute
					top: 25px
					right: 25px
					+s(close-640)
					z-index: 100
					cursor: pointer
		&__inner
			margin-top: 85px
		&__description
			padding: 0px 40px
		&__button
			margin-bottom: 35px
		&__social
			display: none
		&__boxes-wrap
			max-width: 500px
			display: block
			margin: 0 auto
			margin-bottom: 60px
		&__box
			width: 162px
			height: auto
			padding-top: 28px
			margin-right: 7px
			border: none
			&:before, &:after
				display: none	
			.header__item
				&:first-child
					margin-right: 0px

				width: 100%
				height: 48px
				padding: 17px 0px
				border: none
				margin-bottom: 3px
				font-size: 16px
				+ubuntuB
				font-weight: bold
				$color-map: (green: #15b55a, yellow: #f1c40f, violet: #9525a6, purple: #5e1f9f, darkblue: #3e62aa, skyblue: #599bee)
				@each $color, $value in $color-map
					&.is-bg-#{$color}
						background: $value
					&.is-bg-#{$color}.is-active:after
						display: none
			.header__course-date
				top: 0px
				left: 0px
				width: 100%
				height: 24px
				margin-bottom: 4px
				font-size: 18px
				line-height: 24px
				color: #e6e6e6

	+r(580)
		padding: 8px 10px
	
	+r(542)
		&__submenu
			max-width: 100%
	
	+r(520)
		&__boxes-wrap
			max-width: 400px
		&__box
			width: 130px
			margin-right: 5px
		.anchor
			display: none
	
	+r(420)
		padding: 8px 20px
		&__head
			max-width: 100%
			&.is-open
				.header__menu
					padding: 115px 0px
					.header__link
						&.ic-arrow
							padding: 0px 0px
					.header__submenu
						width: 279px
						margin: 0 auto 15px
						border-spacing: 0px	
						.header__course-date
							display: none
						tr
							float: left
							width: 135px
							&:last-child
								margin-left: 9px
						&__link
							float: right
							width: 135px
							margin-bottom: 3px			
				.header__language
					width: 180px
					top: 635px
					&.is-open
						top: 803px
					a:last-child
						margin-left: 60px
				.header__social
					top: 50px
					.header__ic
						margin-right: 24px
						&:last-child
							margin-right: 0px
				.header__menu-close
					top: 5px
					right: 2px
		&__inner
			margin-top: 95px
		&__description
			padding: 0px 0px
		&__boxes-wrap
			display: none
		&__date
			margin-bottom: 60px
		&__button
			width: 100%
			margin-bottom: 70px
		
.theme
	&-bizdev, &-design, &-devops, &-front-end, &-php, &-pm, &-python, &-main
		padding: 50px 20px
		background: #ecf0f1
		&__title
			+ubuntuB
			font-size: 48px
			line-height: 1
			margin-bottom: 25px
		&__name
			font-weight: bold
			text-transform: capitalize
			span
				text-transform: uppercase
		&__place, &__date
			display: inline-block
			font-size: 20px
		&__date
			&:after
				display: inline
				content: ','
		&__coord
			margin-bottom: 90px
			&:after, &:before
				content: ''
				display: none
				margin-right: 8px
				vertical-align: -4px
				+s(calendar-main-24)
			&:before
				display: inline-block
		$calendar-list: bizdev devops front-end php pm python main
		@each $i in $calendar-list
			.theme-#{$i}__coord:before
				+s(calendar-#{$i}-24)
			.theme-#{$i}__coord:after
				+s(calendar-#{$i}-24)
		&__description
			font-size: 16px
			line-height: 24.78px
			margin-bottom: 35px
		&__button
			display: inline-block
			width: 205px
			padding-top: 13px
			padding-bottom: 12px
			background: #be3542
			+ubuntuB
			font-size: 14px
			text-transform: uppercase
			text-decoration: none
			font-weight: bold
			color: #fff
			&:hover
				border: 1px solid #be3542
				border-bottom: 5px solid #be3542
				padding-top: 12px
				padding-bottom: 7px
				background: #fff
				color: #be3542
				transition: color .2s, background .2s
		+r(640)
			&__coord
				position: relative
				&:before
					display: none
				&:after
					position: absolute
					display: block
					bottom: -55px
					left: 50%
					transform: translateX(-50%)
			$calendar-list: bizdev devops front-end php pm python main
			@each $i in $calendar-list
				.theme-#{$i}__coord:before
					+s(calendar-#{$i}-48)
				.theme-#{$i}__coord:after
					+s(calendar-#{$i}-48)
		+r(330)
			margin-top: 0px
			padding: 60px 15px 45px
			&__date:after
				content: ''
				display: none
		
.program
	padding: 60px 0px
	&__title
		+ubuntuB
		font-size: 36px
		font-weight: bold
		line-height: 1
		text-transform: uppercase
		margin-bottom: 10px
	&__comment
		font-size: 18px
		line-height: 1
		margin-bottom: 55px
		span
			text-transform: uppercase
	.act
		max-width: 1180px
		margin: 5px auto
		display: block
		background: #ecf0f1
		&:first-child
			margin: 0px auto
		&__inner
			+clr
			margin-left: 128px
			padding: 19px 20px 17px
			border-left: 8px solid #c4c4c4
		&__time, &__theme
			float: left
			text-align: left
			font-size: 16px
			color: #535353
		&__time
			width: 110px
	.speaker
		position: relative
		margin-bottom: 5px
		background: #fff
		&-wrapper
			max-width: 1180px
			margin: 0 auto
			background: #ecf0f1
		&__inner
			+clr
			margin-left: 128px
			padding: 19px 20px 17px
			border-left: 8px solid #1f6eb1
			background: #fff
			cursor: pointer
			&:hover
				padding: 17px 20px 15px
				border: 2px solid #1f6eb1
				border-left: 8px solid #1f6eb1
		&__time
			float: left
			width: 110px
			text-align: left
			font-size: 16px
			color: #535353
		&__description
			float: left
			font-size: 16px
			color: #535353
			+r(750)
				max-width: 450px
			+r(610)
				max-width: 300px
			+r(460)
				max-width: 250px
			+r(360)
				max-width: 200px
		&__name
			display: inline-block
			font-size: 20px
			&:after
				content: '|'
				display: inline-block
				margin: 0 8px
		&__position
			text-align: left
			display: inline-block
			color: #1f6eb1
		&__theme
			text-align: left
		&__left-col, &__right-col
			text-align: left
		&__resume, &__theme-text
			display: none
		&__open, &__close
			position: absolute
			display: none
			top: 24px
			left: 85%
			+s(arrow-open)
			cursor: pointer
		&__close
			position: absolute
			display: none
			top: 22px
			left: 98%
			+s(close)
			cursor: pointer
			&:hover
				+s(close-hov)
		&__photo
			position: absolute
			display: none
			top: 22px
			left: (128+8+70)+px
			width: 46px
			height: 46px
			border: 1px solid lightgray
			+s(face)
		&:hover
			background: #1f6eb1
			transition: background .2s
			.speaker__open
				display: block
		&.is-open
			.speaker__inner
				cursor: default
			.speaker__description
				max-width: 755px
				+r(1050)
					max-width: 650px
				+r(950)
					max-width: 550px
				+r(750)
					max-width: 450px
				+r(610)
					max-width: 300px
				+r(460)
					max-width: 250px
				+r(360)
					max-width: 200px
			.speaker__left-col
				float: left
				width: 50%
				padding-right: 20px
				border-right: 1px solid #cbdded
			.speaker__name:after
				content: ''
			.speaker__right-col
				float: left
				width: 50%
				padding-left: 20px
			.speaker__theme
				font-size: 18px
			.speaker__resume, .speaker__theme-text
				display: block
				text-align: left
				margin-top: 15px
			.speaker__open
				display: none
			.speaker__close, .speaker__photo
				display: block
	+r(870)
		.act
			width: 100%
			&__inner
				margin-left: 40px
			&__time
				width: 65px
		.speaker
			&-wrapper
				width: 100%
			&__inner
				margin-left: 40px
				padding: 19px 20px 17px
				border-left: 8px solid #1f6eb1
				background: #fff
			&__time
				width: 65px
				line-height: 28px
			&__name
				display: block
				text-align: left
				&:after
					display: none
			&__position
				display: block
				text-align: left
			&__close
				left: 92%
				+s(close-48)
				&:hover
					+s(close-48)
			&__open
				display: block
				top: 22px
				left: 94%
				+s(arrow-open-48)
			&__photo
				left: (60+8+70)+px
			
			&.is-open
				background: #1f6eb1
				.speaker__inner
					padding: 17px 20px 15px
					border: 2px solid #1f6eb1
					border-left: 8px solid #1f6eb1
				.speaker__left-col
					float: none
					width: 100%
					padding-right: 0px
					border-right: none
				.speaker__name, .speaker__position
					padding-left: 60px
				.speaker__resume
					margin-bottom: 30px
				.speaker__right-col
					float: none
					width: 100%
					padding-left: 0px
